"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import Careers from '../careers/page'
 
export default function WhoWeDesignForPage() {
  const [activeMainSection, setActiveMainSection] = useState("");
  const [activeSubSection, setActiveSubSection] = useState("");

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        let main = null;
        let sub = null;

        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            const id = entry.target.id;
            if (id === "DESIGN PROCESS" || id === "WEBSITE PROCESS") {
              main = id;
            } else {
              sub = id;
            }
          }
        });

        if (main) setActiveMainSection(main);
        if (sub) setActiveSubSection(sub);
      },
      {
        threshold: 0.3,
        rootMargin: "0px 0px -50% 0px",
      }
    );

    const idsToObserve = [
      "DESIGN PROCESS",
      "WEBSITE PROCESS",
      "DESIGNTYPE1",
      "DESIGNTYPE2",
      "WEBSITETYPE1",
      "WEBSITETYPE2",
    ];

    idsToObserve.forEach((id) => {
      const el = document.getElementById(id);
      if (el) observer.observe(el);
    });

    return () => {
      idsToObserve.forEach((id) => {
        const el = document.getElementById(id);
        if (el) observer.unobserve(el);
      });
    };
  }, []);

  return (
    <section className="px-6  bg-white text-gray-800">
      <div className="max-w-6xl mx-auto space-y-5">
        <div className="text-center">
          <h1 className="text-4xl sm:text-7xl  font-bold mb-3">Tailored to deliver the best for your business</h1>
          <p className="text-gray-500 text-md xl:pt-5 xl:text-lg max-w-2xl mx-auto">
            Whether you're building a business, a personal brand, or a creator
            platformâ€”our design is built for you.
          </p>
        </div>

        <div className="sticky top-5 z-10 bg-white py-3 ">
          <div className="flex flex-wrap justify-center gap-6">
            {["DESIGN PROCESS", "WEBSITE PROCESS"].map((id) => (
              <Link
                key={id}
                href={`#${id}`}
                className={`relative text-sm xl:text-xl px-4 xl:px-8 py-2 font-medium transition-colors duration-300 ${
                  activeMainSection === id
                    ? "text-white bg-blue-500 rounded-2xl"
                    : "text-white bg-blue-400  hover:text-white rounded-2xl"
                }`}
              >
                {id}
              </Link>
            ))}
          </div>
        </div>

        <div id="DESIGN PROCESS" className=" scroll-mt-24">
          <h2 className="text-2xl font-bold">Design process</h2>
          <div className="">
            
          </div>
          <div className="sticky top-20 z-20 bg-white py-3">
            <div className="flex justify-center gap-6">
              {["DESIGNTYPE1", "DESIGNTYPE2"].map((id) => (
                <Link
                  key={id}
                  href={`#${id}`}
                  className={`relative text-sm px-4 py-2 font-medium transition-colors duration-300 ${
                    activeSubSection === id
                      ? "text-red-600"
                      : "text-gray-700 hover:text-gray-900"
                  }`}
                >
                  {id === "DESIGNTYPE1" && (
                    <>
                    
                      <p className="text-xs pb-2 text-gray-500">For SMEs and Conglomerates for design</p>
                      <h1 className="text-xl font-bold  xl:text-3xl">Agile Design Process design</h1>
                      
                    </>
                  )}
                  {id === "DESIGNTYPE2" && (
                    <>
                     <div className="flex flex-col justify-between ">
                      <p className="text-xs pb-10 xl:pb-2 text-gray-500">For Start up for design</p>
                      <h1 className="text-xl font-bold  xl:text-3xl">Build Fast. Launch Fast. design</h1>
                      </div>
                    </>
                  )}
                  <span
                    className={`absolute left-0 bottom-0 h-[2px] w-full bg-red-600 transition-transform duration-300 origin-left ${
                      activeSubSection === id ? "scale-x-100" : "scale-x-0"
                    }`}
                  />
                </Link>
              ))}
            </div>
          </div>

          <div id="DESIGNTYPE1" className="scroll-mt-24">
            <h2 className="text-xl xl:text-2xl">Designing a responsive website for both - a desktop and mobile, while ensuring the users feel that the brand is selling fashion and not just the products.</h2>
            <div className="flex justify-between xl:mx-30 my-5 text-sm">
              <div>
                <p>Suited for:</p>
                <p>SMEs and Conglomerates</p>
              </div>
              <div className="w-30">
                <p>Time lines:</p>
                <p>6-12 Months</p>
              </div>
              <div>
                <p>Engagement Mode:</p>
                <p>Retainer</p>
              </div>
              
            </div>
            <div className="flex w-1/2">
              <div className="w-1/2  sticky top-30 bg-gray-300 p-4">
                Sticky Left
              </div>
              <div className="w-1/2 overflow-y-auto bg-white p-4">
                Scrollable RightScrollable RightScrollable RightScrollable RightScrollable RightScrollable RightScrollable RightScrollable RightScrollable RightScrollable RightScrollable RightScrollable RightScrollable RightScrollable RightScrollable Right
              </div>
            </div>


            
          </div>

          <div id="DESIGNTYPE2" className="grid grid-cols-1 sm:grid-cols-2 gap-6 scroll-mt-24">
            <div className="p-6 rounded-xl bg-[#F8F9FA] border shadow-sm">
              <h3 className="text-lg font-semibold text-blue-700">Branding</h3>
              <h4 className="text-md font-medium mb-1">Corporate Identity</h4>
              <p className="text-sm text-gray-600">
                We create consistent brand systems that reflect your corporate values.
              </p>
            </div>
            <div className="p-6 rounded-xl bg-[#F8F9FA] border shadow-sm">
              <h3 className="text-lg font-semibold text-blue-700">Marketing</h3>
              <h4 className="text-md font-medium mb-1">Digital Campaigns</h4>
              <p className="text-sm text-gray-600">
                Run ROI-driven digital campaigns to generate leads and drive growth.
              </p>
            </div>
          </div>
        </div>

        <div id="WEBSITE PROCESS" className="space-y-6 scroll-mt-24">
          <h2 className="text-2xl font-bold">Website Process</h2>
          <div className="flex justify-center gap-6">
            {["WEBSITETYPE1", "WEBSITETYPE2"].map((id) => (
              <Link
                key={id}
                href={`#${id}`}
                className={`relative text-sm px-4 py-2 font-medium transition-colors duration-300 ${
                  activeSubSection === id
                    ? "text-red-600"
                    : "text-gray-700 hover:text-gray-900"
                }`}
              >
                {id === "WEBSITETYPE1" && (
                  <>
                    <p className="text-sm text-gray-500">For SMEs and Conglomerates for website</p>
                    <h1 className="text-3xl">Agile Design Process WEBSITE</h1>
                  </>
                )}
                {id === "WEBSITETYPE2" && (
                  <>
                    <p className="text-sm text-gray-500">FOR Start Up for website</p>
                    <h1 className="text-3xl">Build Fast. Launch Fast. WEBSITE</h1>
                  </>
                )}
                <span
                  className={`absolute left-0 bottom-0 h-[2px] w-full bg-red-600 transition-transform duration-300 origin-left ${
                    activeSubSection === id ? "scale-x-100" : "scale-x-0"
                  }`}
                />
              </Link>
            ))}
          </div>

          <div id="WEBSITETYPE1" className="grid grid-cols-1 sm:grid-cols-2 gap-6 scroll-mt-24">
            <div className="p-6 rounded-xl bg-[#F8F9FA] border shadow-sm">
              <h3 className="text-lg font-semibold text-blue-700">Branding</h3>
              <h4 className="text-md font-medium mb-1">Corporate Identity</h4>
              <p className="text-sm text-gray-600">
                We create consistent brand systems that reflect your corporate values.
              </p>
            </div>
            <div className="p-6 rounded-xl bg-[#F8F9FA] border shadow-sm">
              <h3 className="text-lg font-semibold text-blue-700">Marketing</h3>
              <h4 className="text-md font-medium mb-1">Digital Campaigns</h4>
              <p className="text-sm text-gray-600">
                Run ROI-driven digital campaigns to generate leads and drive growth.
              </p>
            </div>
          </div>

          <div id="WEBSITETYPE2" className="grid grid-cols-1 sm:grid-cols-2 gap-6 scroll-mt-24">
            <div className="p-6 rounded-xl bg-[#F8F9FA] border shadow-sm">
              <h3 className="text-lg font-semibold text-blue-700">Branding</h3>
              <h4 className="text-md font-medium mb-1">Corporate Identity</h4>
              <p className="text-sm text-gray-600">
                We create consistent brand systems that reflect your corporate values.
              </p>
            </div>
            <div className="p-6 rounded-xl bg-[#F8F9FA] border shadow-sm">
              <h3 className="text-lg font-semibold text-blue-700">Marketing</h3>
              <h4 className="text-md font-medium mb-1">Digital Campaigns</h4>
              <p className="text-sm text-gray-600">
                Run ROI-driven digital campaigns to generate leads and drive growth.
              </p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
