"use client";
export default function StickyScrollLayout() {
  return (
    <div className="flex w-1/2">
      <div className="w-1/2 h-screen sticky top-0 bg-gray-300 p-4">
        Sticky Left
      </div>
      <div className="w-1/2 h-[200vh] overflow-y-auto bg-white p-4">
        Scrollable Right
      </div>
    </div>
  );
}

